import BaseApiService from "../../BaseApiService";

const DEFAULT_PATH = "/custom/api/login";

class BasicInformationService extends BaseApiService {
  getAll = (start = 0, limit = 10, search = "") => {
    const url = `${DEFAULT_PATH}/search?${search}start=${start}&limit=${limit}`;
    return this.makeGetRequestWithAuth(url);
  };
  saveQde = (obj: any) => {
    const url = `${DEFAULT_PATH}/qde/basic/info`;
    return this.makePostRequestWithAuth(url, obj);
  };
  updateQde = (obj: any) => {
    const url = `${DEFAULT_PATH}/qde/basic/info`;
    return this.makePutRequestWithAuth(url, obj);
  };
  saveDde = (obj: any) => {
    const url = `${DEFAULT_PATH}/dde/basic/info`;
    return this.makePutRequestWithAuth(url, obj);
  };
  getDde = (applicantId: string) => {
    const url = `${DEFAULT_PATH}/basic/info/uid/${applicantId}`;
    return this.makeGetRequestWithAuth(url);
  };
  updateDde = (obj: any) => {
    const url = `${DEFAULT_PATH}/dde/basic/info`;
    return this.makePutRequestWithAuth(url, obj);
  };
  createBasicInformation = (obj: any) => {
    const url = `${DEFAULT_PATH}/dde/basic/info`;
    return this.makePostRequestWithAuth(url, obj);
  };

  // --------------------------- To create a new corporate - organisation details basic info ---------------------- //

  createCorporateBasicInfo = (payload: any) => {
    const endPoint = DEFAULT_PATH + `/dde/basic/info/corporate`;
    return this.makePostRequestWithAuth(endPoint, payload);
  };

  // --------------------------- To update corporate - organisation details basic info ---------------------- //

  updateCorporateBasicInfo = (payload: any) => {
    const endPoint = DEFAULT_PATH + `/dde/basic/info/corporate`;
    return this.makePutRequestWithAuth(endPoint, payload);
  };

  // --------------------------- To get corporate - organisation details basic info ---------------------- //

  getCorporateBasicInfo = (id: string) => {
    const endPoint = DEFAULT_PATH + `/basic/info/corporate/uid/${id}`;
    return this.makeGetRequestWithAuth(endPoint);
  };

  // --------------------------- To generate dependant basic info uid ---------------------- //

  generateDependantId = (loanId: string) => {
    const endPoint = DEFAULT_PATH + `/dde/basic/info/dependent/${loanId}`;
    return this.makePostRequestWithAuth(endPoint);
  };
}

export default new BasicInformationService();
